export const fetchContacts = async () => {
    //const token = "prsqP1CdhA3e2bRsM4o7f7S_wxLwWdyGndU5M3BxSeSGdsE7KaYc6sZXA_KsB8F4KpVzbLbap11DrEUruh7oRRnA";
    try {
        const response = await fetch("https://api.surveysparrow.com/v3/contacts", {
            headers: {
                Authorization: `Bearer <%=iparams.surveysparrow_api_key%>`
            }
        });
        if(!response.ok) {
            throw new Error("Failed to fetch contacts");

        }
        const data = await response.json();
        return data;
    } catch(error) {
        console.error("Error fetching contacts:", error);
        throw error;

    }
};